.message {
    padding-bottom: 116px;
    padding-top: 41px;
    @include md {
        padding-top: 25px; }
    @include sm {
        margin-bottom: 100px; }
    &__title {
        margin-bottom: 20px; }
    &__wrap {
        display: flex;
        justify-content: space-between;
        @include md {
            flex-wrap: wrap;
            justify-content: center; } }
    &__col--left {
        flex: 1 1 670px;
        max-width: 670px;
        padding-right: 40px;
        font-size: 20px;
        font-weight: 300;
        line-height: 1.55;
        color: $dark;
        @include md {
            flex: 1 1 100%;
            max-width: 100%;
            margin-bottom: 30px;
            padding-right: 0; } }
    &__col--right {
        flex: 0 0 374px;
        max-width: 374px;
        text-align: center;
        @include sm {
            flex: 1 1 374px; } }
    &__text {
        text-align: left; } }
